rea\n  display: block\n  max-width: 100%\n  min-width: 100%\n  padding: $textarea-padding\n  resize: vertical\n  &:not([rows])\n    max-height: $textarea-max-height\n    min-height: $textarea-min-height\n  &[rows]\n    height: initial\n  // Modifiers\n  &.has-fixed-size\n    resize: none\n","%checkbox-radio\n  cursor: pointer\n  display: inline-block\n  line-height: 1.25\n  position: relative\n  input\n    cursor: pointer\n  &:hover\n    color: $input-hover-color\n  &[disabled],\n  fieldset[disabled] &,\n  input[disabled]\n    color: $input-disabled-color\n    cursor: not-allowed\n\n.checkbox\n  @extend %checkbox-radio\n\n.radio\n  @extend %checkbox-radio\n  & + .radio\n    +ltr-property(\"margin\", 0.5em, false)\n","$select-colors: $form-colors !default\n\n.select\n  display: inline-block\n  max-width: 100%\n  position: relative\n  vertical-align: top\n  &:not(.is-multiple)\n    height: $input-height\n  &:not(.is-multiple):not(.is-loading)\n    &::after\n      @extend %arrow\n      border-color: $input-arrow\n      +ltr-position(1.125em)\n      z-index: 4\n  &.is-rounded\n    select\n      border-radius: $radius-rounded\n      +ltr-property(\"padding\", 1em, false)\n  select\n    @extend %input\n    cursor: pointer\n    display: block\n    font-size: 1em\n    max-width: 100%\n    outline: none\n    &::-ms-expand\n      display: none\n    &[disabled]:hover,\n    fieldset[disabled] &:hover\n      border-color: $input-disabled-border-color\n    &:not([multiple])\n      +ltr-property(\"padding\", 2.5em)\n    &[multiple]\n      height: auto\n      padding: 0\n      option\n        padding: 0.5em 1em\n  // States\n  &:not(.is-multiple):not(.is-loading):hover\n    &::after\n      border-color: $input-hover-color\n  // Colors\n  @each $name, $pair in $select-colors\n    $color: nth($pair, 1)\n    &.is-#{$name}\n      &:not(:hover)::after\n        border-color: $color\n      select\n        border-color: $color\n        &:hover,\n        &.is-hovered\n          border-color